import React, { useRef, useEffect, useState } from 'react';
import _ from 'lodash';
import styled from 'styled-components';
import { Carousel } from 'antd';
import { Project } from '../models/projects';
import { Theme } from '../theme';

interface ProjectProps {
  data: Project;
  onCarouselClick: (e: any) => void;
  autoPlay: boolean;
}

const Container = styled.figure`
  position: relative;
  display: flex;
  justify-content: space-between;
  h3 > span {
    font-weight: normal;
  }
  .img {
    text-align: center;
    /* width: 320px; */
    height: 160px;
    line-height: 160px;
    background: #364d79;
    overflow: hidden;
  }
`;
const CarouselWrap = styled.div`
  flex-shrink: 0;
  margin-left: 20px;
  width: 320px;
  cursor: pointer;
  visibility: ${(props) => (props as any).isEmpty ? 'hidden': 'visible'};
`
const ImgWrap = styled.div`
  height: 180px;
  overflow: hidden;
  background-color: ${(props) => (props.theme as Theme).color.gray};
  background-image: url(${(props) => (props as any).img});
  background-size: contain;
  background-repeat: no-repeat;
  background-position-x: center;
`;

const ProjectCard: React.FC<ProjectProps> = (props) => {
  const [playTimer, setPlayTimer] = useState(null);
  const carouselRef = useRef();
  useEffect(() => {
    if (props.autoPlay) {
      setPlayTimer(setInterval(() => {
        if (carouselRef.current) carouselRef.current.next();
      }, 1500) as any)
    } else {
      clearInterval(playTimer as any);
    }
  }, [props.autoPlay])
  return (
    <Container>
      <figcaption>
        <h3>
          {props.data.name}
          <span> | {props.data.date}</span>
        </h3>
        {
          _.map(props.data.description, (text, index) => (
            <p key={index}>- {text}</p>
          ))
        }
      </figcaption>
      <CarouselWrap
        onClick={props.onCarouselClick}
        isEmpty={_.isEmpty(props.data.screenshots)}
      >
        {
          props.data.screenshots.length === 1 ?
          <ImgWrap img={props.data.screenshots[0]} />
          :
          <Carousel effect='fade' dotPosition='right' ref={carouselRef}>
            {
              _.map(props.data.screenshots, (img, index) => (
                <ImgWrap key={index} img={img} />
              ))
            }
          </Carousel>
        }
        
      </CarouselWrap>
    </Container>
  );
}

export default ProjectCard;