import React from 'react';
import _ from 'lodash';
import { Drawer } from 'antd';
import styled from 'styled-components';

enum Placement { top = 'top', bottom = 'bottom' }

interface ImageDrawerProps {
  title: string;
  images: any[];
  placement?: Placement;
  onClose: (e: any) => void;
  visible: boolean;
}
const StyledDrawer = styled(Drawer)`
  .ant-drawer-wrapper-body {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
  }
  .ant-drawer-body {
    flex: 1;
    overflow: hidden;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  img {
    max-width: 100%;
    max-height: 100%;
    margin-bottom: 24px;
  }
  img:last-child {
    margin-bottom: 0;
  }
`;
const ImageDrawer: React.FC<ImageDrawerProps> = (props) => {

  return (
    <StyledDrawer
      title={props.title}
      placement={props.placement || 'bottom'}
      onClose={props.onClose}
      visible={props.visible || false}
      height='calc(100% - 60px)'
    >
      {
        _.map(props.images, (image, index) => (
          <img key={index} src={image} />
        ))
      }
    </StyledDrawer>
  )
}
export default ImageDrawer;