import React, { useState, useEffect, useContext, useRef } from 'react';
import { Observable } from 'rxjs';
import _ from 'lodash';
import styled from 'styled-components';

import { Timeline } from 'antd';
import ProjectCard from './ProjectCard';
import { Theme } from '../theme';
import projects from '../models/projects';
import ImageDrawer from './ImageDrawer';
import { AppContext } from '../App';

const StyledTimeline = styled(Timeline)`
  .ant-timeline-item-head {
    border-radius: 0;
  }
  .ant-timeline-item-head-blue {
    border: none;
    background-color: ${(props) => (props.theme as Theme).color.font};
  }
`;

const ProjectList: React.FC = () => {
  const { windowScroll$, windowResize$ } = useContext(AppContext) as any;
  const [winHeight, setWinHeight] = useState(0);
  const [pageYOffset, setPageYOffset] = useState(0);
  const [offsetTopOfCards, setoffsetTopOfCards] = useState(_.map(projects, () => 0));
  const containerRef = useRef();
  const [drawerShowProjectIndex, setDrawerShowProjectIndex] = useState(-1);
  const updateWinOffset = () => {
    const { pageYOffset } = window;
    setPageYOffset(pageYOffset);
  }
  const updateWinHeight = () => {
    const { innerHeight } = window;
    setWinHeight(innerHeight);
  }
  const handleWinScroll = (e: Event) => {
    updateWinOffset();
  }
  const handleWinResize = (e: Event) => {
    updateWinHeight();
  }
  const handleCardClick = (index: number) => {
    setDrawerShowProjectIndex(index);
  }
  const handleDrawerClose = (e: MouseEvent) => {
    setDrawerShowProjectIndex(-1);
  }
  const getOffsetTopOfCards = () => {
    if (!containerRef.current) return;
    const doms = (containerRef.current! as HTMLDivElement).querySelectorAll('.ant-timeline-item');
    let offsetArray = _.map(doms, (dom: HTMLLIElement) => dom.offsetTop);
    setoffsetTopOfCards(offsetArray);
  }
  const countNearestCardInTheMiddleIndex = () => {
    const middleOffset = pageYOffset + (winHeight / 2);
    const absArray = _.map(offsetTopOfCards, (offset) => Math.abs(offset - middleOffset));
    return _.indexOf(absArray, _.min(absArray));
  }
  useEffect(() => {
    updateWinOffset();
    updateWinHeight();
    getOffsetTopOfCards();
    const scrollSubscription = (windowScroll$ as Observable<Event>).subscribe(handleWinScroll);
    const resizeSubscription = (windowResize$ as Observable<Event>).subscribe(handleWinResize);
    return () => {
      scrollSubscription.unsubscribe();
      resizeSubscription.unsubscribe();
    };
  }, []);
  return (
    <div ref={containerRef}>
      <StyledTimeline>
        {
          _.map(projects, (project, index) => (
            <Timeline.Item key={index}>
              <ProjectCard
                autoPlay={countNearestCardInTheMiddleIndex() === index}
                data={project}
                onCarouselClick={(e: any) => {handleCardClick(index)}}
              />
            </Timeline.Item>
          ))
        }
      </StyledTimeline>
      <ImageDrawer
        visible={drawerShowProjectIndex !== -1} 
        title={drawerShowProjectIndex !== -1 ? projects[drawerShowProjectIndex].name : ''}
        images={drawerShowProjectIndex !== -1 ? projects[drawerShowProjectIndex].screenshots : []}
        onClose={handleDrawerClose}
      />
    </div>
  )
}
export default ProjectList;