import React from 'react';
import styled from 'styled-components';
import _ from 'lodash';
import { Theme } from '../theme';
import categories from '../models/skills';

const Container = styled.div`
  display: flex;
  justify-content: space-between;
`;

const Category = styled.div`
  flex: 1;
  > ul {
    list-style: none;
  }
  > h3 {
    margin-bottom: 10px;
  }
  li {
    > b {
      font-weight: 500;
      margin-right: 10px;
    }
    > span {
      font-weight: 100;
    }
  }

`;

const Line = styled.div`
  width: 1px;
  background-color: ${(props) => (props.theme as Theme).color.gray};
  margin: 0 22px;
`;

const Skills: React.FC = () => {
  return (
    <Container>
      {
        _.map(categories, (category, categoryIndex) => ([
          <Category key={`${categoryIndex}-content`}>
            <h3>## {category.label}</h3>
            <ul>
              {
                _.map(category.skills, (skill, skillIndex) => (
                  <li key={skillIndex}>
                    <b>- {skill.name}</b>
                    <span>{skill.description || ''}</span>
                  </li>
                ))
              }
            </ul>
          </Category>,
          categoryIndex !== categories.length - 1 ?
            <Line key={`${categoryIndex}-line`} />
            : null
        ]))
      }
    </Container>
  )
}
export default Skills;