import React from 'react';
import _ from 'lodash';
import { Anchor } from 'antd';
import { anchor } from '../constants';
import styled from 'styled-components';
import { Theme } from '../theme';
import PersonInformation from './PersonalInformation';
const { Link } = Anchor;

export interface HeaderProps {
  anchors: readonly anchor[];
}

const StyledHeader = styled.header`
  width: ${(props) => (props.theme as Theme).pageWidth};
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
`;
const StyledAnchor = styled(Anchor)`
  .ant-anchor {
    display: flex;
    font-size: ${(props) => props.theme.fontSize[0]};
  }
  .ant-anchor-link {
    padding: 0 0 0 16px;
    line-height: 1.5;
  }
  .ant-anchor-ink::before {
    width: 0;
  }
  .ant-anchor-ink-ball {
    display: none;
  }
`;

const Header: React.FC<HeaderProps> = (props) => {
  return (
    <StyledHeader>
      <PersonInformation />
      <StyledAnchor affix={false} offsetTop={80}>
        {
          _.map(props.anchors, (anchor, index) => {
            return <Link key={index} href={`#${anchor.key}`} title={anchor.label} />
          })
        }
      </StyledAnchor>
    </StyledHeader>
  );
}
export default Header;